import React from 'react'
import './Header.css'

export default class Header extends React.Component {
    render() {
        return (
            <div className="header">
                <nav className="navbar navbar-expand-sm bg-dark navbar-dark">
                    <a className="navbar-brand">Offers</a>
                </nav>
            </div>
        );
    }
}


