import React, { Component } from 'react';
import { connect } from 'react-redux'
import { update } from 'global-store-mc'
import Header from './Header'
import './OfferManager.css'

class OfferManager extends Component {
    render() {
        localStorage.setItem('pmText', JSON.stringify(this.props.data))
        return (
            <div style={{ height: '100%' }}>
                <div>
                    <Header />
                </div>
                <div className="row">
                    <form className="center">
                        <div className="form-group">
                            <label>Offers</label>
                            <input className="form-control" type="text" name="name" onChange={(e) => this.props.dispatch(update(e.target.value))} />
                        </div>
                        <div>
                            {this.props.data}
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        data: state.updateText.data
    }
}

export default connect(mapStateToProps)(OfferManager);



